.std-cols {
  gap: 30px;
  display: grid;
}

.std-cols--2 {
  grid-template-columns: repeat(2, 1fr);
}

@media (max-width: 999.98px) {
  .std-cols--2 {
    grid-template-columns: none;
  }
}

.std-cols--3 {
  grid-template-columns: repeat(3, 1fr);
}

@media (max-width: 999.98px) {
  .std-cols--3 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 499.98px) {
  .std-cols--3 {
    grid-template-columns: none;
  }
}

.std-cols--4 {
  grid-template-columns: repeat(4, 1fr);
}

@media (max-width: 999.98px) {
  .std-cols--4 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 499.98px) {
  .std-cols--4 {
    grid-template-columns: none;
  }
}

